#page-topbar {
  position: fixed;
  top: 0;
  right: 0;
  left: 0;
  z-index: 1002;
  background-color: var(--bs-header-bg);
  border-bottom: 1px solid var(--bs-border-color);
}

.navbar-header {
  display: flex;
  -ms-flex-pack: justify;
  justify-content: space-between;
  align-items: center;
  margin: 0 auto;
  height: 70px;
  padding: 0 20px 0 0;
}
.navbar-header .dropdown .show.header-item {
  background-color: var(--bs-gray-100);
}

.navbar-brand-box {
  padding: 0 1.5rem;
  width: 250px;
  background: var(--bs-sidebar-bg);
  border-right: 1px solid var(--bs-sidebar-border-color);
  box-shadow: 0px 2px 2px var(--bs-sidebar-bg);
}

.logo {
  line-height: 69px;
  color: inherit !important;
}
.logo .logo-sm {
  display: none;
}

.logo-txt {
  font-weight: 700;
  font-size: 18px;
  vertical-align: middle;
  margin-left: 5px;
}

.logo-light {
  display: none;
}

body:not([data-sidebar-size=sm]) #vertical-menu-btn {
  margin-left: -52px;
  margin-right: 20px;
}
@media (max-width: 991.98px) {
  body:not([data-sidebar-size=sm]) #vertical-menu-btn {
    margin-left: 0;
  }
}

/* Search */
.app-search {
  padding: calc(30px / 2) 0;
  position: relative;
}
.app-search .form-control {
  border: var(--bs-search-width) solid var(--bs-search-border-color);
  height: 40px;
  padding-left: 17px;
  padding-right: 50px;
  background-color: var(--bs-topbar-search-bg);
  box-shadow: none;
}
.app-search .btn {
  position: absolute;
  right: 3px;
  top: 3px;
  height: 34px;
  padding: 0 10px;
}

.layout-mode-light {
  display: none;
}

body[data-layout-mode=dark] .layout-mode-dark {
  display: none;
}
body[data-layout-mode=dark] .layout-mode-light {
  display: inline-block;
}

.megamenu-list li {
  position: relative;
  padding: 5px 0px;
}
.megamenu-list li a {
  color: var(--bs-body-color);
}

@media (max-width: 992px) {
  .navbar-brand-box {
    width: auto;
  }
  .logo span.logo-lg {
    display: none;
  }
  .logo span.logo-sm {
    display: inline-block;
  }
}
.page-content {
  padding: calc(70px + 20px) calc(20px / 2) 60px calc(20px / 2);
}

.header-item {
  height: 70px;
  box-shadow: none !important;
  color: var(--bs-header-item-color);
  border: 0;
  border-radius: 0px;
}
.header-item:hover {
  color: var(--bs-header-item-color);
}

.header-profile-user {
  height: 36px;
  width: 36px;
  background-color: var(--bs-gray-600);
  padding: 3px;
}

.noti-icon i {
  font-size: 22px;
  color: var(--bs-header-item-color);
}
.noti-icon .badge {
  position: absolute;
  top: 12px;
  right: 4px;
}

.notification-item .d-flex {
  padding: 0.75rem 1rem;
}
.notification-item .d-flex:hover {
  background-color: var(--bs-dropdown-link-hover-bg);
}

.dropdown-icon-item {
  display: block;
  border-radius: 3px;
  line-height: 34px;
  text-align: center;
  padding: 15px 0 9px;
  display: block;
  color: #74788d;
}
.dropdown-icon-item img {
  height: 24px;
}
.dropdown-icon-item span {
  display: block;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.dropdown-icon-item:hover {
  background-color: var(--bs-gray-100);
}

.fullscreen-enable [data-toggle=fullscreen] .bx-fullscreen::before {
  content: "\ea3f";
}

body[data-topbar=dark] #page-topbar {
  background-color: #313533;
  border-color: var(--bs-gray-100);
}
body[data-topbar=dark] .navbar-brand-box {
  background-color: #313533;
  border-color: #313533;
  box-shadow: 0px 0px 2px #313533;
}
body[data-topbar=dark] .navbar-brand-box .logo {
  color: #fff !important;
}
body[data-topbar=dark] .navbar-header .dropdown .show.header-item {
  background-color: rgba(255, 255, 255, 0.05);
}
body[data-topbar=dark] .navbar-header .waves-effect .waves-ripple {
  background: rgba(255, 255, 255, 0.4);
}
body[data-topbar=dark] .header-item {
  color: #e9ecef;
}
body[data-topbar=dark] .header-item:hover {
  color: #e9ecef;
}
body[data-topbar=dark] .header-item.border-start, body[data-topbar=dark] .header-item.border-end {
  border-color: rgba(255, 255, 255, 0.1) !important;
}
body[data-topbar=dark] .header-item.bg-soft-light {
  background-color: rgba(255, 255, 255, 0.04) !important;
}
body[data-topbar=dark] .header-profile-user {
  background-color: rgba(255, 255, 255, 0.25);
}
body[data-topbar=dark] .noti-icon i {
  color: #e9ecef;
}
body[data-topbar=dark] .logo-dark {
  display: none;
}
body[data-topbar=dark] .logo-light {
  display: block;
}
body[data-topbar=dark] .app-search .form-control {
  background-color: var(--bs-topbar-dark-search-bg);
  color: var(--bs-gray-500);
}
body[data-topbar=dark] .app-search span,
body[data-topbar=dark] .app-search input.form-control::-webkit-input-placeholder {
  color: rgba(255, 255, 255, 0.5);
}

@media (max-width: 600px) {
  .navbar-header .dropdown {
    position: static;
  }
  .navbar-header .dropdown .dropdown-menu {
    left: 10px !important;
    right: 10px !important;
  }
}
@media (max-width: 380px) {
  .navbar-brand-box {
    display: none;
  }
}
body[data-layout=horizontal] .navbar-brand-box {
  width: auto;
  border: 0;
  background-color: transparent;
  box-shadow: none;
}
@media (min-width: 992px) {
  body[data-layout=horizontal] .navbar-brand-box {
    padding-left: 0;
    margin-right: 10px;
  }
}
body[data-layout=horizontal] .page-content {
  margin-top: 70px;
  padding: calc(55px + 20px) calc(20px / 2) 60px calc(20px / 2);
}
@media (min-width: 992px) {
  body[data-layout=horizontal] .navbar-header {
    padding-left: 20px;
    padding-right: 20px;
  }
}
body[data-layout=horizontal][data-sidebar=dark] .navbar-brand-box {
  background-color: transparent;
  box-shadow: none;
}

@media (max-width: 992px) {
  body[data-layout=horizontal] .page-content {
    margin-top: 15px;
  }
}
.page-title-box {
  padding-bottom: 20px;
}
.page-title-box .breadcrumb {
  background-color: transparent;
  padding: 0;
}

.footer {
  bottom: 0;
  padding: 20px calc(20px / 2);
  position: absolute;
  right: 0;
  color: var(--bs-footer-color);
  left: 250px;
  height: 60px;
  background-color: var(--bs-footer-bg);
  border-top: 1px solid var(--bs-border-color);
}
@media (max-width: 991.98px) {
  .footer {
    left: 0;
  }
}

body[data-sidebar-size=sm] .footer {
  left: 70px;
}
@media (max-width: 991.98px) {
  body[data-sidebar-size=sm] .footer {
    left: 0;
  }
}

body[data-layout=horizontal] .footer {
  left: 0 !important;
}

.right-bar {
  background-color: var(--bs-card-bg);
  box-shadow: 0 0 24px 0 rgba(0, 0, 0, 0.06), 0 1px 0 0 rgba(0, 0, 0, 0.02);
  display: block;
  position: fixed;
  transition: all 200ms ease-out;
  width: 300px;
  z-index: 9999;
  float: right !important;
  right: -310px;
  top: 0;
  bottom: 0;
}
.right-bar .right-bar-toggle {
  background-color: #444c54;
  height: 24px;
  width: 24px;
  line-height: 24px;
  display: block;
  color: #e9e9ef;
  text-align: center;
  border-radius: 50%;
}
.right-bar .right-bar-toggle:hover {
  background-color: #4b545c;
}

.rightbar-overlay {
  background-color: rgba(52, 58, 64, 0.55);
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  display: none;
  z-index: 9998;
  transition: all 0.2s ease-out;
}

.right-bar-enabled .right-bar {
  right: 0;
}
.right-bar-enabled .rightbar-overlay {
  display: block;
}

@media (max-width: 767.98px) {
  .right-bar {
    overflow: auto;
  }
  .right-bar .slimscroll-menu {
    height: auto !important;
  }
}
.metismenu {
  margin: 0;
}
.metismenu li {
  display: block;
  width: 100%;
}
.metismenu .mm-collapse {
  display: none;
}
.metismenu .mm-collapse:not(.mm-show) {
  display: none;
}
.metismenu .mm-collapse.mm-show {
  display: block;
}
.metismenu .mm-collapsing {
  position: relative;
  height: 0;
  overflow: hidden;
  transition-timing-function: ease;
  transition-duration: 0.35s;
  transition-property: height, visibility;
}

.vertical-menu {
  width: 250px;
  z-index: 1001;
  background: var(--bs-sidebar-bg);
  bottom: 0;
  margin-top: 0;
  position: fixed;
  top: 70px;
  border-right: 1px solid var(--bs-sidebar-border-color);
}

.main-content {
  margin-left: 250px;
  overflow: hidden;
}
.main-content .content {
  padding: 0 15px 10px 15px;
  margin-top: 70px;
}

#sidebar-menu {
  padding: 10px 0 30px 0;
}
#sidebar-menu .mm-active > .has-arrow:after {
  transform: rotate(90deg);
}
#sidebar-menu .has-arrow:after {
  content: "\f0142";
  font-family: "Material Design Icons";
  display: block;
  float: right;
  transition: transform 0.2s;
  font-size: 1.1rem;
  margin-right: -5px;
  margin-top: -2px;
}
#sidebar-menu ul li a {
  display: block;
  padding: 0.62rem 1.5rem;
  color: var(--bs-sidebar-menu-item-color);
  position: relative;
  font-size: 0.9rem;
  transition: all 0.4s;
  font-weight: 500;
}
#sidebar-menu ul li a i {
  display: inline-block;
  min-width: 1.75rem;
  padding-bottom: 0.125em;
  font-size: 1.25rem;
  line-height: 1.40625rem;
  vertical-align: middle;
  color: var(--bs-sidebar-menu-item-icon-color);
  transition: all 0.4s;
}
#sidebar-menu ul li a svg {
  height: 16px;
  width: 16px;
  color: var(--bs-sidebar-menu-item-icon-color);
  fill: var(--bs-sidebar-menu-item-icon-effact-color);
  margin-right: 10px;
  margin-top: -3px;
}
#sidebar-menu ul li a:hover {
  color: var(--bs-sidebar-menu-item-hover-color);
}
#sidebar-menu ul li a:hover i {
  color: var(--bs-sidebar-menu-item-hover-color);
}
#sidebar-menu ul li a:hover svg {
  color: var(--bs-sidebar-menu-item-hover-color);
  fill: var(--bs-sidebar-menu-item-hover-effact-color);
}
#sidebar-menu ul li .badge {
  margin-top: 4px;
}
#sidebar-menu ul li ul.sub-menu {
  padding: 0;
}
#sidebar-menu ul li ul.sub-menu li a {
  padding: 0.4rem 1.5rem 0.4rem 3.3rem;
  font-size: 0.85rem;
  color: var(--bs-sidebar-menu-sub-item-color);
}
#sidebar-menu ul li ul.sub-menu li a:hover {
  color: var(--bs-sidebar-menu-item-hover-color);
}
#sidebar-menu ul li ul.sub-menu li ul.sub-menu {
  padding: 0;
